.call-to-action {
  margin-top: 93px;
  box-sizing: border-box;
}

.call-to-action__wrapper {
  display: flex;
  width: 1176px;
  margin: 0 auto;
  padding: 0 15px;
  justify-content: space-between;
  box-sizing: border-box;
}

.call-to-action__inner-wrapper {
  max-width: 340px;
  box-sizing: border-box;
}

.call-to-action__title {
  margin-top: 21px;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 17px;
  line-height: 150%;
  color: $black;
  opacity: 0.87;
}

.call-to-action__desc {
  font-weight: normal;
  font-size: 13px;
  line-height: 150%;
  color: $black;
  opacity: 0.5;
}

.call-to-action__fieldset {
  display: flex;
  align-items: center;
  margin: 15px 4px 0 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
}

.call-to-action__form-item {
  margin: 0;
  padding: 0;
  width: auto;
  box-sizing: border-box;
}

.call-to-action__form-item input {
  width: 250px;
  margin-right: 24px;
  padding: 20px 38px 20px 38px;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 1px;
  color: rgba($color: $black, $alpha: 0.87);
  border: 2px solid #e3e3e3;
  border-radius: 80px;
  box-sizing: border-box;
  outline: none;
  transition: border ease 0.3s;

  &::placeholder {
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    color: rgba($color: $black, $alpha: 0.5);
  }

  &:required:valid {
    border: $form-valid;
  }

  &:hover,
  &:focus {
    border: 2px solid $yellow;
  }

  &:-moz-submit-invalid {
    box-shadow: none;
  }

  &:-moz-ui-invalid {
    box-shadow: none;
  }
}

.call-to-action__form-item .visually-hidden:active {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.call-to-action__btn-submit {
  @include btn;

  padding: 20px 39px;
  outline: none;

  &:hover,
  &:focus {
    @include btn-hover;

    padding: 20px 39px;
  }

  &:active {
    @include btn-active;

    padding: 20px 39px;
  }
}

@media (max-width: 1200px) {
  .call-to-action {
    margin-top: 50px;
  }

  .call-to-action__wrapper {
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  .call-to-action__inner-wrapper {
    width: 100%;
    text-align: center;
    max-width: 550px;
    margin-left: auto;
    margin-right: auto;
  }

  .call-to-action__title {
    font-size: 22px;
  }

  .call-to-action__desc {
    font-size: 16px;
  }

  .call-to-action__form-item {
    width: 33%;
    margin-right: 20px;
  }

  .call-to-action__form-item input {
    width: 100%;
  }

  .call-to-action__btn-submit {
    width: 33%;
  }
}

@media (max-width: $mobile) {
  .call-to-action {
    margin-top: 0;
    padding: o;
  }

  .call-to-action__wrapper {
    width: 100%;
    margin: 0;
    padding: 0 15px;
  }

  .call-to-action__inner-wrapper {
    margin: 0;
    max-width: none;
  }

  .call-to-action__title {
    margin-top: 6px;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: $black;
    text-align: left;
    opacity: 0.87;
  }

  .call-to-action__desc {
    text-align: left;
    margin-bottom: 18px;
    font-size: 13px;
  }

  .call-to-action__form {
    width: 100%;
  }

  .call-to-action__fieldset {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .call-to-action__form-item {
    width: 100%;
    margin-right: 0;
  }

  .call-to-action__form-item input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 9px;
    padding: 18px 38px;
  }

  .call-to-action__btn-submit {
    @include btn;

    width: auto;
    margin-top: 12px;
    margin-left: 0;
    padding: 20px 63px;

    &:hover,
    &:focus {
      @include btn-hover;

      padding: 20px 63px;
    }

    &:active {
      @include btn-active;

      padding: 20px 63px;
    }
  }
}
